import React, { useState, useEffect, useRef } from 'react';
import logo from '../assets/img/ico_logo.png';

function Signup({ onSignupSuccess, onBackToLogin }) {
  //    
  const [email, setEmail] = useState('');
  const [verificationCode, setVerificationCode] = useState('');
  const [sentCode, setSentCode] = useState('');
  const [isEmailVerified, setIsEmailVerified] = useState(false);
  const [isCodeSent, setIsCodeSent] = useState(false);
  const [timeLeft, setTimeLeft] = useState(0); //   ()
  const timerRef = useRef(null);

  //    
  const [name, setName] = useState('');
  const [nickname, setNickname] = useState('');
  const [password, setPassword] = useState('');
  const [confirmPassword, setConfirmPassword] = useState('');
  const [thumbnail, setThumbnail] = useState(''); //   (base64)

  //  
  const [toast, setToast] = useState({ show: false, message: '' });
  const toastTimerRef = useRef(null);

  //  
  useEffect(() => {
    if (timeLeft > 0 && !isEmailVerified) {
      timerRef.current = setInterval(() => {
        setTimeLeft((prev) => {
          if (prev <= 1) {
            clearInterval(timerRef.current);
            showToast(' . .');
            setSentCode(''); //   
            return 0;
          }
          return prev - 1;
        });
      }, 1000);

      return () => {
        if (timerRef.current) {
          clearInterval(timerRef.current);
        }
      };
    }
  }, [timeLeft, isEmailVerified]);

  //     
  useEffect(() => {
    return () => {
      if (timerRef.current) {
        clearInterval(timerRef.current);
      }
      if (toastTimerRef.current) {
        clearTimeout(toastTimerRef.current);
      }
    };
  }, []);

  //   
  const showToast = (message) => {
    setToast({ show: true, message });
    
    if (toastTimerRef.current) {
      clearTimeout(toastTimerRef.current);
    }
    
    toastTimerRef.current = setTimeout(() => {
      setToast({ show: false, message: '' });
    }, 3000);
  };

  //  mm:ss  
  const formatTime = (seconds) => {
    const minutes = Math.floor(seconds / 60);
    const secs = seconds % 60;
    return `${minutes}:${secs.toString().padStart(2, '0')}`;
  };

  //   
  const handleThumbnailChange = (e) => {
    const file = e.target.files[0];
    if (file) {
      //    (2MB )
      if (file.size > 2 * 1024 * 1024) {
        showToast('  2MB  .');
        return;
      }

      //   
      if (!file.type.startsWith('image/')) {
        showToast('   .');
        return;
      }

      const reader = new FileReader();
      reader.onloadend = () => {
        setThumbnail(reader.result);
      };
      reader.readAsDataURL(file);
    }
  };

  //  
  const removeThumbnail = () => {
    setThumbnail('');
  };

  //   
  const isEmailValid = (email) => {
    const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
    return emailRegex.test(email);
  };

  //    (6 , ++)
  const isPasswordValid = (password) => {
    const passwordRegex = /^(?=.*[A-Za-z])(?=.*\d)(?=.*[@$!%*#?&])[A-Za-z\d@$!%*#?&]{6,}$/;
    return passwordRegex.test(password);
  };

  //  
  const handleSendVerification = () => {
    if (!isEmailValid(email)) {
      showToast('   .');
      return;
    }

    //    
    const users = JSON.parse(localStorage.getItem('users') || '[]');
    if (users.find(u => u.email === email)) {
      showToast('  .');
      return;
    }

    //  6  
    const code = Math.floor(100000 + Math.random() * 900000).toString();
    setSentCode(code);
    setIsCodeSent(true);
    setTimeLeft(180); // 3 = 180
    setVerificationCode(''); //   

    //    API  , 
    //    
    console.log(`[] ${email}  : ${code}`);
    showToast(` ! (: ${code})`);
  };

  //  
  const handleVerifyCode = () => {
    if (timeLeft === 0) {
      showToast(' . .');
      return;
    }

    if (verificationCode === sentCode) {
      setIsEmailVerified(true);
      if (timerRef.current) {
        clearInterval(timerRef.current);
      }
      showToast('  .');
    } else {
      showToast('  .');
    }
  };

  //    
  const isFormValid = () => {
    return (
      isEmailVerified &&
      name.trim() !== '' &&
      nickname.trim() !== '' &&
      password !== '' &&
      confirmPassword !== '' &&
      isPasswordValid(password) &&
      password === confirmPassword
    );
  };

  //  
  const handleSignup = (e) => {
    e.preventDefault();

    if (!isFormValid()) {
      showToast('   .');
      return;
    }

    if (password !== confirmPassword) {
      showToast('  .');
      return;
    }

    if (!isPasswordValid(password)) {
      showToast(' 6 , ++  .');
      return;
    }

    //   
    const users = JSON.parse(localStorage.getItem('users') || '[]');
    users.push({
      email,
      password,
      name,
      nickname,
      thumbnail: thumbnail || '', //    
      createdAt: new Date().toISOString()
    });
    localStorage.setItem('users', JSON.stringify(users));

    //  
    localStorage.setItem('currentUser', email);
    showToast(' !');
    setTimeout(() => {
      onSignupSuccess(email);
    }, 1000);
  };

  return (
    <div className="login-container">
      <div className="login-box signup-box">
        <div className="login-header">
          <h1 className="login-title">
            
            <img src={logo} alt="Todoit Logo" className="login-logo" />
          </h1>
          <p className="login-subtitle">  </p>
        </div>

        <form onSubmit={handleSignup} className="login-form">
          {/*    */}
          <div className="login-form-group">
            <label htmlFor="email" className="form-label"></label>
            <div className="input-with-button">
              <input
                id="email"
                type="email"
                className="text-input"
                placeholder="example@email.com"
                value={email}
                onChange={(e) => setEmail(e.target.value)}
                disabled={isEmailVerified}
                readOnly={isEmailVerified}
              />
              <button
                type="button"
                className="btn-common btn-primary btn-verify"
                onClick={handleSendVerification}
                disabled={!isEmailValid(email) || isEmailVerified}
              >
                {isCodeSent ? '' : ''}
              </button>
            </div>
          </div>

          {isCodeSent && !isEmailVerified && (
            <div className="login-form-group">
              <label htmlFor="verificationCode" className="form-label"></label>
              <div className="input-with-button">
                <div className="input-with-timer">
                  <input
                    id="verificationCode"
                    type="text"
                    className="text-input"
                    placeholder="6 "
                    value={verificationCode}
                    onChange={(e) => setVerificationCode(e.target.value)}
                    maxLength={6}
                    disabled={timeLeft === 0}
                  />
                  {timeLeft > 0 && (
                    <span className="timer-text">{formatTime(timeLeft)}</span>
                  )}
                </div>
                <button
                  type="button"
                  className="btn-common btn-primary  btn-verify"
                  onClick={handleVerifyCode}
                  disabled={verificationCode.length !== 6 || timeLeft === 0}
                >
                   
                 </button>
               </div>
             </div>
           )}

          {/*     */}
          <div className="user-info-section">
            <div className="user-info-left">
              <div className="login-form-group">
                <label htmlFor="name" className="form-label"> </label>
                <input
                  id="name"
                  type="text"
                  className="text-input"
                  placeholder=""
                  value={name}
                  onChange={(e) => setName(e.target.value)}
                />
              </div>

              <div className="login-form-group">
                <label htmlFor="nickname" className="form-label"></label>
                <input
                  id="nickname"
                  type="text"
                  className="text-input"
                  placeholder=""
                  value={nickname}
                  onChange={(e) => setNickname(e.target.value)}
                />
              </div>
            </div>

            <div className="user-info-right">
              <label className="form-label screen-out"></label>
              <div className="thumbnail-upload">
                <input
                  type="file"
                  id="thumbnail"
                  accept="image/*"
                  onChange={handleThumbnailChange}
                  style={{ display: 'none' }}
                />
                <div className="thumbnail-wrapper">
                  <label htmlFor="thumbnail" className="thumbnail-preview">
                    {thumbnail ? (
                      <img src={thumbnail} alt=" " className="thumbnail-image" />
                    ) : (
                      <div className="thumbnail-placeholder">
                        <img src={logo} alt=" " className="thumbnail-default-icon" />
                      </div>
                    )}
                  </label>
                  {thumbnail && (
                    <button
                      type="button"
                      className="thumbnail-remove"
                      onClick={(e) => {
                        e.preventDefault();
                        removeThumbnail();
                      }}
                      aria-label=" "
                    >
                      
                    </button>
                  )}
                </div>
              </div>
            </div>
          </div>

          <div className="login-form-group">
            <label htmlFor="password" className="form-label"></label>
            <input
              id="password"
              type="password"
              className="text-input"
              placeholder="6 , ++"
              value={password}
              onChange={(e) => setPassword(e.target.value)}
              autoComplete="new-password"
            />
            {password && !isPasswordValid(password) && (
              <p className="input-hint error">
                6 , ++  .
              </p>
            )}
          </div>

          <div className="login-form-group">
            <label htmlFor="confirmPassword" className="form-label"> </label>
            <input
              id="confirmPassword"
              type="password"
              className="text-input"
              placeholder="  "
              value={confirmPassword}
              onChange={(e) => setConfirmPassword(e.target.value)}
              autoComplete="new-password"
            />
            {confirmPassword && password !== confirmPassword && (
              <p className="input-hint error">  .</p>
            )}
          </div>

          <button
            type="submit"
            className="btn-common btn-primary"
            disabled={!isFormValid()}
          >
            
          </button>
        </form>

        <div className="login-footer">
          <button
            type="button"
            className="login-toggle-btn"
            onClick={onBackToLogin}
          >
              ? 
          </button>
        </div>
      </div>

      {/*   */}
      {toast.show && (
        <div className="toast-message">
          {toast.message}
        </div>
      )}
    </div>
  );
}

export default Signup;
